import { Box, Button, Typography } from '@mui/material'

import CONFERENCE_CATEGORIES from 'src/data/conferenceCategories'
import type { ConferenceCategory } from 'src/data/conferenceCategories'

import { PureLightTheme } from 'src/theme/schemes/PureLightTheme'

interface ConferenceCategoryProps {
  selectedCategories: ConferenceCategory[]
  onCategoryChange: (categories: ConferenceCategory[]) => void
}

const ConferenceCategorySelector: React.FC<ConferenceCategoryProps> = ({ selectedCategories, onCategoryChange }) => {
  const handleCategoryToggle = (category: ConferenceCategory) => {
    if (selectedCategories.includes(category)) {
      onCategoryChange(selectedCategories.filter(cat => cat !== category))
    } else {
      onCategoryChange([...selectedCategories, category])
    }
  }

  return (
    <Box>
      <Typography variant="h4" color={PureLightTheme.colors.primary.main} sx={{ margin: '20px 0px' }}>
        행사 카테고리
      </Typography>
      <Box sx={{ display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)', gap: 1.5 }}>
        {CONFERENCE_CATEGORIES.map(category => (
          <Button
            key={category}
            onClick={() => handleCategoryToggle(category)}
            variant="outlined"
            sx={{
              py: 1,
              textTransform: 'none',
              fontSize: '0.95rem',
              borderColor: PureLightTheme.colors.alpha.black[30],
              backgroundColor: selectedCategories.includes(category)
                ? PureLightTheme.colors.primary.lighter
                : 'transparent',
              color: selectedCategories.includes(category)
                ? PureLightTheme.colors.primary.light
                : PureLightTheme.colors.secondary.light,
              '&:hover': {
                backgroundColor: selectedCategories.includes(category)
                  ? PureLightTheme.colors.primary.lighter
                  : 'transparent',
                borderColor: PureLightTheme.colors.primary.light
              }
            }}
          >
            {category}
          </Button>
        ))}
      </Box>
    </Box>
  )
}

export default ConferenceCategorySelector
